// Mixin itself
@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

.wrapper {
  min-height: 100vh;
  background: url('/img/wrapper-bg.jpg') top center no-repeat #111;
  font-family: Roboto, sans-serif;
}
.header {
  height: 350px;
  border-radius: 50px;
  box-shadow: 2px 2px 10px 0px #111;
  background-image: linear-gradient(to bottom, #3bad44 0%, #041303 100%);
}
.content {
  background-color: #6c9a9d;
  flex-grow: 1;
}
.footer {
  height: 200px;
  background-color: #8a8a8a;
}

